body{
  background-image: url("images/background.jpg");
}

.heading{
  font-family: 'Neucha', cursive;
  padding-left: 30px;
  background-color: #675b9e;
  padding-top: 0.1px;
  color: white;
  font-size: 18px;
}

.App {
  margin: 0 auto;
  max-width: 800px;
  padding: 20px;
}

.panels {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 20px;
}

.panel {
  flex: 0 1 25%;
  margin: 10px;
}

textarea {
  font-family: 'Neucha', cursive;
  width: 100%;
  height: 70px;
  resize: none;
  border: 1px solid #ccc;
  padding: 10px;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

button {
  font-family: 'Neucha', cursive;
  padding: 15px 30px;
  border: none;
  background-color: #675b9e;
  color: white;
  cursor: pointer;
  font-weight: bold;
  border-radius: 25px;
  transition: background-color 0.3s ease;

}

button:hover {
  background-color: #432d71;
}

button:disabled {
  opacity: 0.5;
  cursor: default;
  background-color: #ccc;
}

.comic-strip {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.comic-strip img {
  margin: 10px;
  object-fit: cover;
}
